.statistics-list {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #e2422d;
    font-size: 14px;
    width: 100%;
    list-style-type: none;
    @media (max-width: 770px) {
        & {
            display: none;
        }
    }
    &__column {
        width: 555px;
        display: flex;
        justify-content: space-between;
        @media (max-width: 990px) {
            & {
                width: 420px;
            }
        }
    }
    &__column-container {
        width: 555px;
        display: flex;
        justify-content: space-between;
        list-style-type: none;
    }
    &__paragraph {
        line-height: 36px;
        font-family: 'Roboto', sans-serif;
        @media (max-width: 990px) {
            &:last-child {
                display: none;
            }
        }
    }
    &__value {
        background-color: #e2422d;
        padding: 0 8px;
        line-height: 36px;
        font-family: 'Russo One', sans-serif;
        font-weight: 400;
        border-radius: 20px;
        color: #ffdd4d;
        font-size: 13px;
    }
    &__key { 
        line-height: 36px;
        display: inline-block;
    }
}
