.header {
    display: flex;
    justify-content: center;
    @media (max-width: 990px) {
        overflow: hidden;
        @media (max-width: 770px) {
            & {
                overflow: visible;
                position: fixed;
                top: 0;
                left: 0;
                width: 100vw;
                height: 110px;
                z-index: 100;
            }
        }
    }
    &__logo {
        margin-top: 10px;
    }
    &__burger {
        display: none;
        @media (max-width: 770px) {
            & { 
                position: absolute;
                top: 62px;
                left: 55px;
                display: block;
                width: 32px;
                height: 18px;
            }
            &-line {
                position: absolute;
                transition: 0.2s;
                width: 100%;
                border-radius: 3px;
                background-color: #000000;
                height: 4px;
            }
            &-line_first {
                top: 0;
            }
            &-line_last {
                top: 100%;
            }
            &-line_middle {
                top: 50%;
            }
            &_active &-line {
                position: absolute;
            }
            &_active &-line_first {
                top: 50%;
                transform: translateY(-50%) rotate(-45deg);
            }
            &_active &-line_last {
                top: 50%;
                transform: translateY(-50%) rotate(45deg);
            }
            &_active &-line_middle {
                opacity: 0;
            }
        }
    }
    &__wraper {
        transition: 0.3s;
        height: 230px;
        width: 1600px;
        min-width: 1600px;
        background-color: #ff8100;
        display: flex;
        justify-content: center;
        align-items: flex-end;
        background-image: linear-gradient(180deg, #fffa00, #ff8100);
        border-radius: 0 0 50% 50%/0 0 100% 100%;
        margin-top: -70px;
        box-shadow: 0 24px 64px 0 rgba(0, 0, 0, 0.05);
        @media (max-width: 990px) {
            & {
                margin-top: 0;
                background-image: linear-gradient(180deg,#fffa00 -30%,#ff8100);
            }
            @media (max-width: 770px) {
                & {
                    top: -40px;
                    height: 140px;
                    min-width: 110vw;
                    width: 110vw;
                    position: absolute;
                    left: 50%;
                    transform: translateX(-50%);
                    border-radius: 0 0 50% 50%;
                    background-image: linear-gradient(180deg, #fffa00, #ff8100);
                    box-shadow: 0 24px 64px 0 rgba(0, 0, 0, 0.05);
                }
                &_open {
                    height: auto;
                    align-items: normal;
                    padding-top: 50px;
                    padding-bottom: 50px;
                    border-radius: 0;
                }
            }
        }
    }
    &__container {
        width: 930px;
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        @media (max-width: 990px) {
            & {
                width: 70vw;
            }
            @media (max-width: 770px) {
                &_hidden_mob {
                    display: none;
                }
            }
        }
    }
    &__container_nav {
        justify-content: space-between;
        @media (max-width: 990px) {
            & {
                justify-content: center;
            }
        }
    }
    &__nav {
        width: 720px;
        margin-bottom: 75px;
        @media (max-width: 770px) {
            & {
                margin: 0;
                width: 70vw;
                display: flex;
                justify-content: center;
            }
        }
    }
}
